* {
font-family: 'Space Mono', monospace;
}
body{
  background-color: hsl(185, 41%, 84%);
  padding: 0;
  margin: 0;
}
/*
- Very dark cyan: hsl(183, 100%, 15%)
- Dark grayish cyan: hsl(186, 14%, 43%)
- Dark grayish cyan: hsl(184, 14%, 56%)
- Light grayish cyan: hsl(185, 41%, 84%)
- Light grayish cyan: hsl(189, 41%, 97%)
- White: hsl(0, 0%, 100%)
*/

#root{
  display: flex;
  justify-content: center;
}
.App {
  width: 1440px;
  height: 100%;
  margin-top: 150px;
  display: flex;
  justify-content: center;
}

.container {
  background-color: #fff;
  width: 60%;
  height: 460px;
  border-radius: 25px;
  padding: 35px;
}
.container form {
  height: 100%;
}
.numbersSection {
  width: 50%;
  height: 50%;
  float: left;
  border-radius: 20px;
  position: relative;
}
.numbersSection label {
  display: block;
  margin-bottom: 10px;
  font-weight: bold;
  color: hsl(186, 14%, 43%);
}
.errorMessage {
  color: #e37254;
  font-weight: bold;
  position: absolute;
  right: 15px;
  top: -15px;
  display: none;
}
.errorMessage2 {
  color: #e37254;
  font-weight: bold;
  position: absolute;
  right: 15px;
  top: 320px;
  display: none;
}
.numbersSection input {
  width: 90%;
  padding: 7px 15px;
  border: 0;
  outline: 0;
  border-radius: 0.25rem;
  font-weight: bold;
  font-size: 1.7rem;
  background-color: hsl(189, 41%, 97%);
  text-align: right;
  margin-bottom: 35px;
  cursor: pointer;
  color: hsl(183, 100%, 15%);
}
.numbersSection input:focus {
  box-shadow: inset 0px 0px 0px 2px hsl(172, 67%, 45%);
  outline: 0;
}
.selectTip {
  margin-bottom: 32px;
}
.selectTip button {
  background-color: hsl(183, 100%, 15%);
  color: #fff;
  font-size: 1.5rem;
  font-weight: bold;
  border: 0;
  border-radius: 5px;
  padding: 15px;
  width: 30%;
  margin: 5px;
}
.selectTip button:hover {
  background-color: hsl(185, 41%, 84%);
  cursor: pointer;
}
.selectTip input {
  padding: 15px;
  width: 22.5%;
  margin: 5px;
  text-align: center;
  font-size: 1.5rem;

}




.totalSection {
  background-color: hsl(183, 100%, 15%);
  width: 40%;
  height: 78%;
  float: right;
  border-radius: 20px;
  padding: 35px;
  color: #fff;
  position: relative;
}
.totalSection div{
  width: 100%;
  height: 30%;
  position: relative;
}
.totalSection div h3{
  float: left;
  line-height: 25px;
}
.totalSection div h3 span{
  color: hsl(184, 14%, 56%);
  font-size: 0.95rem;
}
.totalSection div p{
  float: right;
  color: hsl(172, 67%, 45%);
  font-weight: bold;
  font-size: 2.6rem;
  position: absolute;
  right: 0;
  top: -30px;
}
.totalSection button[type="reset"] {
  width: 87%;
  height: 60px;
  border-style: none;
  border-radius: 10px;
  cursor: pointer;
  background-color: hsl(172, 67%, 45%);
  font-size: 1.5rem;
  font-weight: bold;
  position: absolute;
  bottom: 35px;
}

.totalSection button[type="reset"]:hover {
  background-color: hsl(185, 41%, 84%);
}



@media (max-width: 1166px) and (min-width: 375px) {
  .container {
    width: 60%;
    height: 1000px;
    margin-bottom: 60px;
  }
  .numbersSection {
    width: 106%;
    float: left;
  }
  .totalSection {
    width: 88%;
    float: inline-end;
    height: 35%;
    border-radius: 20px;
  }
  .selectTip input {
    width: 25%;
    
  
  }
}